body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  position: fixed;
  width: 100%;
  z-index: 1;
  background-color: #F9DB79;
  color: black;
  padding: 5px 0;
}

.logo{
  width: 50px;
  margin-left: 50px;
}

.topo{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav a{
  color: black;
  padding-left: 5px;
  padding-right: 20px;
  text-decoration: none;
  font-weight: bold;
}
nav a:hover{
  color: white;
  font-weight: bolder;
}

/* ============ RESPONSIVO ========== */
@media (max-width:425px){
  .logo{
    margin: 0 auto;
  }
  .topo{
    flex-direction: column;
    justify-content: center;
  }
  nav{
    width: 90%;
    border-top: 1px solid rgba(24, 23, 23, 0.575);
    text-align: center;
  }
  nav a{
    padding: 3px 40px;
  }
}
footer{
  width: 100%;
  background-color: black;
  color: white;
}
.rodape{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 100px 20px;
}
.rodape div{
  width: 20%;
}

.logo-footer{
  width: 100px;
  height: auto;
}

.info span{
  text-transform: uppercase;
  font-weight: bolder;
}
.footer-autoral{
  padding: 20px 0;
  text-align: center;
}

/* ============= RESPONSIVO ============== */
@media (max-width:768px){
  .rodape{
    padding: 40px 20px;
    flex-direction: column;
  }
  .rodape div{
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }

  .footer-autoral{
    padding: 5px 20px;
  }
}
.banner{
  width: 100%;
  height: 90vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('/assets/imagens/banner-home.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: rgb(249, 219, 121);
}
.texto-banner{
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 20px 30px;
}
.texto-banner h1{
  color: white;
  font-size: 100px;
  text-shadow: 3px 5px 2px #474747;
}

main{
  display: flex;
  flex-direction: column;
  background-color: white;
  color: black;
}

.card{
  width: 100%;
  display: flex;
}
.card img{
  width: 50%;
  height: auto;
  object-fit: cover;
}
.card-text{
  display: flex;
  width: 50%;
  flex-direction: column;
  align-items: center;
  text-align: justify;
  justify-content: center;
  padding: 20px 40px;
}
.titulo-card{
  text-transform: uppercase;
  font-size: 30px;
  line-height: 26px;
  text-align: center;
}
.card-text span{
  font-weight: bold;
  font-style: italic;
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
}
.card-text p{
  margin-top: 40px;
  text-align: center;
  line-height: 26px;
}

/* ================= RESPONSIVO =================*/
@media (max-width:425px){
  .banner{
    height: 100vh;
  }
  .texto-banner h1{
    font-size: 40px;
    flex-wrap: wrap;
  }

  .card-text h3{
    font-size: 30px;
  }
}

@media (max-width:768px){
  .texto-banner h1{
    font-size: 70px;
  }
  .card{
    flex-direction: column;
  }
  .card img{
    width: 100%;
  }
  .card-text{
    width: 100%;
    text-align: center;
    padding: 40px 100px;
  }
  .card-text h3{
    font-size: 25px;
    line-height: 10px;
  }
  .card:nth-child(odd){
    flex-direction: column-reverse;
  }
}
.sabor{
  background-image: url('/assets/imagens/banner-sabores.jpg');
  background-size: cover;
}

.sabores{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-top: 60px;
  text-align: center;
}

.galeria{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 20px;
  justify-content: space-between;
}
.card-sorvete{
  width: 30%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  line-height: 26px;
  border-radius: 5px;
  box-shadow: 2px 2px 15px 2px #d9dde1;
  margin-bottom: 30px;
}
.card-sorvete img{
  width: 100%;
  height: 250px;
  object-fit: contain;
}

/*==================== RESPONSIVO ====================*/
@media (max-width:768px){
  .texto-banner h1{
    font-size: 60px;
  }

  .galeria{
    flex-direction: column;
    align-items: center;
  }
  
  .card-sorvete{
    width: 100%;
    padding: 20px 40px;
    margin-bottom: 20px;
  }
}

@media (max-width:436px){
  .texto-banner h1{
    font-size: 40px;
  }   
}
.sobre{
  background-image: url('/assets/imagens/banner-sobre.png');
  background-size: cover;
}

.texto-sobre{
  text-align: center;
  padding: 60px 20px;
}
.texto-sobre h3{
  font-weight: bolder;
  font-size: 25px;
}
.texto-sobre span{
  font-weight: 600;
  font-style: italic;
}
.texto-sobre p{
  margin-top: 10px;
  font-size: 15px;
}
.texto-sobre p:first-of-type{
  margin-top: 50px;
}

.imagens{
  display: flex;
}
.imagens img{
  width: 50%;
  height: auto;
  object-fit: cover;
}

/* =============== RESPONSIVO =============== */
@media (max-width:768px){
  .texto-banner h1{
    font-size: 60px;
  }
  
  .imagens{
    flex-direction: column;
  }
  .imagens img{
    width: 100%;
  }
}

@media (max-width:436px){
  .texto-banner h1{
    font-size: 40px;
  }   
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.wrap{
  max-width: 1024px;
  margin: 0 auto;
}
